.station__status table.gridjs-table tbody tr:hover {
  background: #f4f4f4;
  cursor: pointer;
}

.section__grid__wrap.station__detail {
  grid-template-columns: repeat(5, minmax(0, 1fr));
}

.content.station__detail section.station__status__box .top {
  padding: 12px 8px 0;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 60px;
}

.content.station__detail section.station__status__box>.section__inner {
  padding: 0px;
}

.content.station__detail section.station__status__box>.section__inner>.top>p {
  letter-spacing: -0.025em;
  font-size: 1.1rem;
  padding: 0 16px;
  white-space: nowrap;
  color: rgb(107, 114, 128);
}

.content.station__detail section.station__status__box:nth-of-type(1) .section__inner>dl {
  --tw-text-opacity: 1;
  color: rgb(119 119 119 / var(--tw-text-opacity));
}

.content.station__detail section.station__status__box:nth-of-type(2) .section__inner>dl {
  --tw-text-opacity: 1;
  color: rgb(255 193 7 / var(--tw-text-opacity));
}

.content.station__detail section.station__status__box:nth-of-type(3) .section__inner>dl {
  --tw-text-opacity: 1;
  color: rgb(76 175 80 / var(--tw-text-opacity));
}

.content.station__detail section.station__status__box:nth-of-type(4) .section__inner>dl {
  --tw-text-opacity: 1;
  color: rgb(33 150 243 / var(--tw-text-opacity));
}

.content.station__detail section.station__status__box:nth-of-type(5) .section__inner>dl {
  --tw-text-opacity: 1;
  color: rgb(230 81 0 / var(--tw-text-opacity));
}

.content.station__detail section.station__status__box>.section__inner>dl {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 8px 12px;
  box-sizing: border-box;
  margin: 14px 0;
  line-height: 1.5;
}

.content.station__detail section.station__status__box>.section__inner>dl dd {
  margin-top: 6px;
}

.content.station__detail section.station__status__box>.section__inner>dl dt {
  letter-spacing: -0.025em;
  line-height: 1;
  font-size: 4.4rem;
  min-height: 61.6px;
}

.content.station__detail section.station__status__box>.section__inner>dl dt.txt {
  font-size: 3rem;
  line-height: 61.6px;
}

.content.station__detail .sub__nav {
  grid-column: span 5 / span 5;
}

.content.station__detail .sub__nav .right button {
  margin-right: 12px;
}

.content.station__detail .sub__nav .right button:last-of-type {
  margin-right: 0px;
}

.content.station__detail section.battery__status>.section__inner {
  display: flex;
  justify-content: space-between;
}

.content.station__detail section.battery__status {
  grid-column: span 5 / span 5;
}

.content.station__detail section.battery__status>.section__inner>div:first-of-type {
  width: 600px;
}

.content.station__detail section.battery__status>.section__inner>div:nth-of-type(2) {
  width: calc(100% - 630px);
}

.content.station__detail section.battery__status>.section__inner>div:nth-of-type(2) table {
  width: calc(100% - 630px);
}

.content.station__detail section.battery__status>.section__inner>div:nth-of-type(2) .common__grid {
  overflow-x: auto;
  overflow-y: hidden;
  max-height: 380px;
}

.content.station__detail section.battery__status>.section__inner>div>.top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.content.station__detail section.battery__status>.section__inner>div>.top .normal__title {
  font-size: 1.1rem;
  color: rgb(107, 114, 128);
}

.content.station__detail section.battery__detail {
  grid-column: span 5 / span 5;
}

.content.station__detail section.battery__detail .common__grid {
  overflow-x: auto;
}

.content.station__detail section.psu__status {
  grid-column: span 5 / span 5;
}

.content.station__detail section.io__status {
  grid-column: span 5 / span 5;
}

.content.station__detail section.fan__status {
  grid-column: span 5 / span 5;
}

.content.station__detail section.summary__input {
  grid-column: span 5 / span 5;
}

.content.station__detail section.summary__input .text__input__wrap textarea {
  width: 100%;
  height: 90px;
  outline: none;
  padding: 12px;
  box-sizing: border-box;
  border: 1px solid #d0d0d0;
}

.content.station__detail section.system__used .top ul {
  width: 100%;
}

.content.station__detail section.system__used .top ul li dl {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content.station__detail section.system__used .top>ul>li:first-of-type {
  margin-bottom: 12px;
}

.content.station__detail section.system__used .top>ul>li dl dd {
  color: var(--main-color);
}

.content.station__detail section.system__used .chart__wrap {
  width: 100%;
  margin-top: 24px;
  margin-bottom: 18px;
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  overflow: hidden;
}

.content.station__detail section.system__used .chart__wrap>div {
  width: 100%;
  height: 100%;
  max-height: 100% !important;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content.station__detail section.system__used .top>ul>li:nth-of-type(2) dl dd {
  background: rgba(0, 0, 0, 0.08);
  padding: 2px 8px;
  color: rgb(17, 24, 39);
  border-radius: 100px;
}

.content.station__detail section.system__used .top ul li dl dt {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.content.station__detail section.system__used .bottom li:first-of-type dl {
  border-bottom: 1px solid rgb(226, 232, 240);
}

.content.station__detail section.system__used .bottom dl {
  padding: 12px 0;
  display: flex;
  justify-content: space-between;
}

.content.station__detail section.system__used .bottom dl dt {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-indent: 16px;
  position: relative;
}

.content.station__detail section.system__used .bottom dl dt:after {
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 100px;
  position: absolute;
  left: 0px;
  top: 50%;
  -webkit-transform: translateY(-50%);
}

.content.station__detail .system__used:nth-of-type(11) .bottom li:first-of-type dl dt:after {
  background: rgb(49, 130, 206);
}

.content.station__detail .system__used:nth-of-type(11) .bottom li:nth-of-type(2) dl dt:after {
  background: rgb(99, 179, 237);
}

.content.station__detail .system__used:nth-of-type(12) .bottom li:first-of-type dl dt:after {
  background: rgb(49, 151, 149);
}

.content.station__detail .system__used:nth-of-type(12) .bottom li:nth-of-type(2) dl dt:after {
  background: rgb(79, 209, 197);
}

.content.station__detail .system__used:nth-of-type(13) .bottom li:first-of-type dl dt:after {
  background: rgb(221, 107, 32);
}

.content.station__detail .system__used:nth-of-type(13) .bottom li:nth-of-type(2) dl dt:after {
  background: rgb(246, 173, 85);
}

.content.station__detail .system__used:nth-of-type(14) .bottom li:first-of-type dl dt:after {
  background: rgb(128, 90, 213);
}

.content.station__detail .system__used:nth-of-type(14) .bottom li:nth-of-type(2) dl dt:after {
  background: rgb(183, 148, 244);
}

.content.station__detail .system__used:nth-of-type(15) .bottom li:first-of-type dl dt:after {
  background: rgb(213, 90, 90);
}

.content.station__detail .system__used:nth-of-type(15) .bottom li:nth-of-type(2) dl dt:after {
  background: rgb(244, 148, 148);
}

.content.station__detail section.system__used .bottom dl dd {
  color: rgb(107, 114, 128);
}

.content.station__detail .section__title {
  grid-column: span 5 / span 5;
}

.content.station__detail section.status__grid .common__grid {
  overflow-x: auto;
}

.content.station__detail section.status__grid tbody tr span {
  display: block;
  border: 1px solid rgb(186, 186, 186);
  width: 15px;
  height: 15px;
  border-radius: 100px;
  box-sizing: border-box;
}

.content.station__detail section.status__grid tbody tr span.status1 {
  background: rgb(249, 162, 0);
}

.content.station__detail section.status__grid tbody tr span.status2 {
  background: rgb(255, 0, 0);
}

.content.station__detail section.status__grid tbody tr span.status3 {
  background: rgb(0, 255, 0);
}

.content.station__detail section.status__grid tbody tr span.status4 {
  background: rgb(0, 0, 255);
}

@media (max-width: 1000px) {
  .section__grid__wrap.station__detail {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .content.station__detail .sub__nav {
    grid-column: span 2 / span 2;
  }

  .content.station__detail section.battery__status>.section__inner {
    flex-direction: column;
  }

  .content.station__detail section.battery__status {
    grid-column: span 2 / span 2;
  }

  .content.station__detail>.page__title__wrap {
    grid-column: span 2 / span 2;
  }

  .content.station__detail section.battery__detail {
    grid-column: span 2 / span 2;
  }

  .content.station__detail section.psu__status {
    grid-column: span 2 / span 2;
  }

  .content.station__detail section.io__status {
    grid-column: span 2 / span 2;
  }

  .content.station__detail section.fan__status {
    grid-column: span 2 / span 2;
  }

  .content.station__detail .section__title {
    grid-column: span 2 / span 2;
  }

  .content.station__detail section.summary__input {
    grid-column: span 2 / span 2;
  }

  .content.station__detail section.battery__status>.section__inner>div:first-of-type {
    width: 100%;
    margin-bottom: 24px;
  }

  .content.station__detail section.battery__status>.section__inner>div:nth-of-type(2) {
    width: 100%;
  }
}

@media (max-width: 800px) {
  .content.station__detail .sub__nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .content.station__detail .sub__nav .left {
    margin-bottom: 12px;
  }
}

@media (max-width: 660px) {
  .section__grid__wrap.station__detail {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }

  .content.station__detail .sub__nav {
    grid-column: span 1 / span 1;
  }

  .content.station__detail section.battery__status {
    grid-column: span 1 / span 1;
  }

  .content.station__detail section.battery__detail {
    grid-column: span 1 / span 1;
  }

  .content.station__detail section.psu__status {
    grid-column: span 1 / span 1;
  }

  .content.station__detail>.page__title__wrap {
    grid-column: span 1 / span 1;
  }

  .content.station__detail section.io__status {
    grid-column: span 1 / span 1;
  }

  .content.station__detail section.fan__status {
    grid-column: span 1 / span 1;
  }

  .content.station__detail .section__title {
    grid-column: span 1 / span 1;
  }

  .content.station__detail section.summary__input {
    grid-column: span 1 / span 1;
  }

  .content.station__detail .sub__nav .left {
    margin-bottom: 0px;
  }

  .content.station__detail .sub__nav .right button {
    margin-top: 12px;
  }
}